import {
  WebSocketProvider,
  Grid,
  HemisphereLightStage,
  Hands,
  MotionControllers,
  ContribLoader,
  SceneContainer,
  SceneFileDropContainer,
  useSocket,
} from '@vuer-ai/vuer';
import { Helmet } from '@vuer-ai/react-helmet-async';
import React from 'react';
import ReactDOM from 'react-dom';
import * as VUER from '@vuer-ai/vuer';
import * as THREE from 'three';
import * as JSX from 'react/jsx-runtime';
import * as FIBER from '@react-three/fiber';
import * as XR from '@react-three/xr';
import * as Drei from '@react-three/drei';
import * as LEVA from 'leva';
import * as ZUSTAND from 'zustand/index';
import { Leva } from 'leva';

declare global {
  interface Window {
    React: typeof React;
    ReactDOM: typeof ReactDOM;
    VUER: typeof VUER;
    THREE: typeof THREE;
    JSX: typeof JSX;
    FIBER: typeof FIBER;
    XR: typeof XR;
    Drei: typeof Drei;
  }
}

if (typeof window !== 'undefined') {
  window.React = React;
  window.ReactDOM = ReactDOM;
  window.VUER = VUER;
  window.THREE = THREE;
  window.JSX = JSX;
  window.FIBER = FIBER;
  window.XR = XR;
  window.Drei = Drei;
  window.LEVA = LEVA;
  window.ZUSTAND = ZUSTAND;
}

function MainView() {
  const { downlink } = useSocket();

  return (
    <SceneFileDropContainer
      stream={downlink}
      style={{
        display: 'flex',
        flexDirection: 'row',
      }}
      innerStyle={{
        display: 'flex',
        flexDirection: 'row',
      }}
    >
      <SceneContainer
        stream={downlink}
        editMode={true}
        bgChildren={[
          <Grid key='grid' _key='grid' />,
          <HemisphereLightStage key='default-light-stage' />,
          <Hands key='hands' />,
          <MotionControllers key='motion-controllers' fps={30} stream />,
        ]}
      >
        <ContribLoader
          _key='contrib-loader-mujoco'
          // prodOnly
          library='@vuer-ai/mujoco-ts'
          version='0.0.59'
          main='dist/index.umd.js'
        />
      </SceneContainer>
    </SceneFileDropContainer>
  );
}

function Page() {
  const isSSR = typeof window === 'undefined';

  if (isSSR) return null;

  return (
    <>
      <Helmet>
        <title>Vuer Studio</title>
      </Helmet>
      <main className={'studio-page h-screen w-screen overflow-hidden'}>
        {!isSSR ? (
          <WebSocketProvider>
            <MainView />
            <Leva
              theme={{
                sizes: {
                  rootWidth: '380px',
                  controlWidth: '200px',
                  numberInputMinWidth: '56px',
                },
              }}
            />
          </WebSocketProvider>
        ) : null}
      </main>
    </>
  );
}

export default Page;
